import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import { createTheme } from '@mui/material/styles';
import Ailerons from './fonts/Ailerons.woff';
import EnchantedAmulet from './/images/enchantedamulet.jpeg';
import { ThemeProvider } from "@mui/material/styles";
import React, { useState } from "react";
import { Stripe } from "@stripe/stripe-js";
import Checkout from './checkout';
import { CartContext, CartProvider } from "../cartContext";
import { useContext } from 'react';
import { productsArray } from './productStore';


const theme = createTheme({
  typography: {
    fontFamily: 'Ailerons',
  },
  components: {
    MuiCssBaseline: {
      styleOverrides: `
        @font-face {
          font-family: 'Ailerons';
          font-style: normal;
          font-display: swap;
          font-weight: 200;
          src: local('Raleway'), local('Raleway-Regular'), url(${Ailerons}) format('woff2');
          unicodeRange: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF;
        }
      `,
    },
  },
});

const buttonStyles1 = {
  fontSize: "13px",
  textAlign: "center",
  color: "#000",
  padding: "5px 20px",
  boxShadow: "2px 5px 10px rgba(0,0,0,.1)",
  backgroundColor: "rgb(75, 217, 242)",
  borderRadius: "6px",
  letterSpacing: "1.5px",
}
const buttonStyles2 = {
  borderWidth:'1px',
  fontSize: "13px",
  textAlign: "center",
  color: "#000",
  padding: "5px 20px",
  backgroundColor: "rgb(204, 237, 237)",
  borderRadius: "6px",
  letterSpacing: "1.5px",
}

function MediaCard(props) { // props.product is the product we are selling
  const product = props.product;
  const cart = useContext(CartContext);
  const productQuantity = cart.getProductQuantity(product.id);
  return (

    <Card
      sx={{ maxWidth: 800, minWidth: 350 }}>
      <CardMedia
        src={product.image}
        alt={product.title}
        component='img'
        height="250"
      />
      <CardContent>
        <Typography gutterBottom variant="h3" component="div">
          {product.title}
        </Typography>
        <Typography variant="h5" color="text.secondary">
          {product.description}
        </Typography>
      </CardContent>
      <CardActions>
        <Checkout price_id = {[product.price_id]}>Purchase</Checkout>
        {productQuantity > 0 ?
          <button  style = {buttonStyles2} onClick={() => cart.removeOneFromCart(product.id)} >Remove</button>
          :
          <button  style = {buttonStyles1} onClick={() => cart.addOneToCart(product.id)}> Add To Cart</button>
        }
        {/* <Button size="large" >Learn More</Button> */}

      </CardActions>
    </Card>
  );
}


// const Item = styled(Paper)(({ theme }) => ({
//   backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
//   ...theme.typography.body2,
//   padding: theme.spacing(1),
//   textAlign: 'center',
//   color: theme.palette.text.secondary,
// }));

const FeaturedItems = () => {
  return (
    <div>
      <ThemeProvider theme={theme}>
        <div style={{ margin: '0 auto 0 auto', width: '100%' }}>
          <Box alignItems="center" justifyContent="center" display="flex" margin={{ xs: '0 1%', sm: "0 2%", md: '0 5%', lg: '0 10%', xl: '0 8%' }} width={{ xs: "20%", sm: "80%", md: '85%', lg: '85%', xl: '85%' }}>
            <Grid container spacing={{ xs: '1px', sm: '10px', md: '5px' }}>
              {productsArray.map((product, idx) => (
                <React.Fragment key={idx}>
                  <Grid item xs={12} sm={12} md={12} lg={6} xl={4}>
                    <MediaCard product={product}></MediaCard>
                  </Grid></React.Fragment>
              ))}
            </Grid>
          </Box>
        </div>
      </ThemeProvider>
    </div>
  );
};
export default FeaturedItems;