import React, { useState } from "react";
import { loadStripe } from "@stripe/stripe-js";

const buttonStyles = {
  fontSize: "13px",
  textAlign: "center",
  color: "#000",
  padding: "5px 20px",
  boxShadow: "2px 5px 10px rgba(0,0,0,.1)",
  backgroundColor: "rgb(201, 102, 255)",
  borderRadius: "6px",
  letterSpacing: "1.5px",
}

const buttonDisabledStyles = {
  opacity: "0.5",
  cursor: "not-allowed",
}

let stripePromise;
const getStripe = () => {
  if (!stripePromise) {
    stripePromise = loadStripe("pk_test_51Okf1IJJvQ9ibgP2BaA8hyAdqXjWwimXumt9ClmxBU9Pp03iB2SmhtYV1pun0NgiFKlL6iL5gY5Xmc8bP0FWD3jv007ymDS3uQ")
  }
  return stripePromise;
}

const Checkout = (prop) => {

  const price_id = prop.price_id;
  let lineItems = [];
  for (var i = 0 ;i < price_id.length; i++){
    lineItems.push({'price':price_id.at(i), "quantity":1})
  }
  const [loading, setLoading] = useState(false)

  const redirectToCheckout = async event => {
    event.preventDefault();
    setLoading(true);

    const stripe = await getStripe();
    const { error } = await stripe.redirectToCheckout({
      mode: "payment",
      lineItems: lineItems,
      successUrl: `http://localhost:8000/success/`,
      cancelUrl: `http://localhost:8000/`,
    })

    if (error) {
      console.warn("Error:", error);
      setLoading(false);
    }
  }

  return (
    <button
      disabled={loading}
      style={
        loading ? { ...buttonStyles, ...buttonDisabledStyles } : buttonStyles
      }
      onClick={redirectToCheckout}
    >
      PURCHASE
    </button>
  )
}

export default Checkout