import React, { useState, useContext } from 'react'
// import { FaBars, FaTimes } from 'react-icons/fa'
import Logo from "./logo"
import './NavBar.css'; // Assuming you have a CSS file for styling
import { Link } from 'gatsby';
import { Modal, Button,  Nav, Container} from 'react-bootstrap'
import { CartContext } from "../cartContext";
import CartProduct from './CartProduct';
import Checkout from "./checkout"
import { productsArray, getProductData } from './productStore';
const Navbar = () => {
  const cart = useContext(CartContext);
  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const productsCount = cart.items.reduce((sum, product) => sum + product.quantity, 0);
  var priceID = [];
  cart.items.map(item=>{
    const productData = getProductData(item.id);
    priceID.push(productData.price_id);
  })

  return (
    <>
      <nav className="navbar">
        {/* <Navbar.Toggle aria-controls="responsive-navbar-nav"></Navbar.Toggle> */}
        <ul className="nav-links">
          <li><Link to="/"><strong>Home</strong></Link></li>
          <li><Link to="/gallery/"><strong>Gallery</strong></Link></li>
          <li><a onClick={handleShow}>
            <strong style={{ marginLeft: "1%", marginTop: "-50%" }}>Cart</strong></a></li>
        </ul>
      </nav>
      <Modal show={show} onHide={handleClose}>
        <Modal.Header >
          <Modal.Title>Shopping Cart</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {productsCount > 0 ?
            <>
              <p>Items in your cart:</p>
              {cart.items.map((currentProduct, idx) => (
                <CartProduct key={idx} id={currentProduct.id} quantity={currentProduct.quantity}></CartProduct>
              ))}

              <h1>Total: {cart.getTotalCost().toFixed(2)}</h1>

              <Checkout price_id= {priceID}>
                Checkout
              </Checkout>
            </>
            :
            <h1>There are no items in your cart!</h1>
          }
        </Modal.Body>

      </Modal>
    </>
  );
};

export default Navbar;

